import {
  chooseCreateSourceCatchMessage,
  formatComponentTriggerErrors,
  handleCreateSourceValidation, runCreateSourceAndHandleResponse
} from './controller-create-source-utils';

function createIdealData(sourceRequest, iban) {
  return Object.assign({}, sourceRequest.ideal, {
    customerIban: iban
  });
}


function createDataRetrievalPromise(ibanComponent) {
  const ibanPromise = ibanComponent.send('getComponentData', {});
  return ibanPromise;
}


export function handleIdealCreateSource(ibanComponent, sourceRequest, apiKey, redirectReceiverReturnURL) {
  if (!ibanComponent) {
    return Promise.reject('Cannot create source because all the required components have not been created for an iDeal submission');
  }

  if (!ibanComponent.send) {
    return Promise.reject('Cannot create source because all the required components have not been mounted for an iDeal submission');
  }

  const ibanDataPromise = createDataRetrievalPromise(ibanComponent);

  return ibanDataPromise
    .then((componentValue) => {
      const iban = componentValue.data;
      // trigger event so that change events with errors get sent

      const triggerIdealEvent = ibanComponent.send('componentTriggerEvent', {type: 'createSource'});

      return triggerIdealEvent.then((triggerValues) => {
        const errors = formatComponentTriggerErrors([triggerValues]);

        if (errors.length > 0) {
          return handleCreateSourceValidation(errors);
        }

        sourceRequest.ideal = createIdealData(sourceRequest, iban);
        if (typeof sourceRequest.ideal.returnUrl === 'undefined') {
          sourceRequest.ideal.returnUrl = redirectReceiverReturnURL;
        }

        return runCreateSourceAndHandleResponse(sourceRequest, apiKey);

      }).catch(function (error) {
        return chooseCreateSourceCatchMessage(error);
      });


    }).catch(function (error) {
      return chooseCreateSourceCatchMessage(error);
    });
}